          <el-option :key="2" :value="2" label="å¸‚çº§ç”¨æˆ·" v-if="this.$store.state.user.role == 0">å¸‚çº§ç”¨æˆ·</el-option>
          <el-option :key="3" :value="3" label="åŽ¿çº§ç”¨æˆ·" v-if="this.$store.state.user.role == 0">åŽ¿çº§ç”¨æˆ·</el-option>
          <el-option
