              @change.native="getCurrentRow(scope.$index)"
            >&nbsp</el-radio>
          </template>
        </el-table-column>
        <el-table-column prop="username" label="ç”¨æˆ·å" align="center"></el-table-column>
        <el-table-column prop="name" label="å§“å" align="center"></el-table-column>
        <el-table-column label="ç”¨æˆ·ç±»åž‹" align="center">
          <template slot-scope="scope">{{userTypes[scope.row.role].label}}</template>
        </el-table-column>
        <el-table-column prop="province" label="çœä»½" align="center"></el-table-column>
        <el-table-column prop="city" label="å¸‚åŒº" align="center"></el-table-column>
        <el-table-column prop="area" label="åŽ¿" align="center"></el-table-column>
        <el-table-column label="æ¿€æ´»çŠ¶æ€" align="center">
          <template slot-scope="scope">{{scope.row.active == 1 ? 'æ¿€æ´»' : 'ç¦ç”¨'}}</template>
        </el-table-column>
        <!-- <el-table-column
          label="æ“ä½œ"
          align="center"
          v-if="this.$store.state.user.role == 0 || this.$store.state.user.role == 4"
        >
          <template slot-scope="scope">
            <el-button v-if="scope.row.role==5" size="mini">ç¼–è¾‘å…³è”</el-button>
          </template>
        </el-table-column> -->
