      <div class="block">
        <el-pagination
          background
          @current-change="handleUserDataCurrentPageChanged"
          :current-page.sync="userData.page"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="userData.total"
        ></el-pagination>
      </div>
    </div>

    <!-- ç”¨æˆ·æ·»åŠ &ç¼–è¾‘å¯¹è¯æ¡†éƒ¨åˆ† -->
    <el-dialog :title="userInfoDialog.isAdd? 'æ·»åŠ ç”¨æˆ·': 'ç¼–è¾‘ç”¨æˆ·'" :visible.sync="userInfoDialog.visible">
      <el-form
        :model="userInfoDialog.form"
        :label-position="labelposition"
        :rules="userInfoDialog.rules"
        ref="userInfoDialogForm"
      >
        <div id="userInfoDialogData">
          <div>
            <el-form-item
              label="ç”¨æˆ·å"
              label-width="80px"
              size="mini"
              prop="username"
              v-if="userInfoDialog.isAdd"
            >
              <el-input
                v-model="userInfoDialog.form.username"
                autocomplete="off"
                style="width: 150px"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="å¯†ç "
              label-width="80px"
              size="mini"
              prop="password"
              v-if="userInfoDialog.isAdd"
            >
              <el-input
                v-model="userInfoDialog.form.password"
                auto-complete="off"
                type="password"
                style="width: 150px"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="é‡å¤å¯†ç "
              label-width="80px"
              size="mini"
              prop="repeat_password"
              v-if="userInfoDialog.isAdd"
            >
              <el-input
                v-model="userInfoDialog.form.repeat_password"
                auto-complete="off"
                type="password"
                style="width: 150px"
              ></el-input>
            </el-form-item>
            <el-form-item label="å§“å" label-width="80px" size="mini" prop="name">
              <el-input v-model="userInfoDialog.form.name" auto-complete="off" style="width: 150px"></el-input>
            </el-form-item>
            <el-form-item label="é‚®ç®±" label-width="80px" size="mini" prop="email">
              <el-input
                v-model="userInfoDialog.form.email"
                auto-complete="off"
                style="width: 150px"
              ></el-input>
            </el-form-item>
            <el-form-item label="æ‰‹æœº" label-width="80px" size="mini" prop="phone">
              <el-input
                v-model="userInfoDialog.form.phone"
                auto-complete="off"
                style="width: 150px"
              ></el-input>
            </el-form-item>
            <el-form-item label="æ¿€æ´»" label-width="80px" size="mini" prop="active">
              <el-checkbox name="active" v-model="userInfoDialog.form.active"></el-checkbox>
            </el-form-item>
          </div>
          <div v-if="userInfoDialog.isAdd">
            <el-form-item label="è§’è‰²" size="mini" label-width="80px" prop="role">
              <el-select v-model="userInfoDialog.form.role" placeholder="ç”¨æˆ·ç±»åž‹" style="width: 150px">
                <el-option
                  :key="1"
                  :value="1"
                  label="çœçº§ç”¨æˆ·"
                  v-if="this.$store.state.user.role == 0"
                >çœçº§ç”¨æˆ·</el-option>
                <el-option
                  :key="2"
                  :value="2"
                  label="å¸‚çº§ç”¨æˆ·"
                  v-if="this.$store.state.user.role == 0"
                >å¸‚çº§ç”¨æˆ·</el-option>
                <el-option
                  :key="3"
                  :value="3"
                  label="åŽ¿çº§ç”¨æˆ·"
                  v-if="this.$store.state.user.role == 0"
                >åŽ¿çº§ç”¨æˆ·</el-option>
                <el-option
                  :key="4"
                  :value="4"
                  label="ç®¡ç†å‘˜"
                  v-if="this.$store.state.user.role == 3 || this.$store.state.user.role == 0 "
                >ç®¡ç†å‘˜</el-option>
                <el-option
                  :key="5"
                  :value="5"
                  label="å·¥äºº"
                  v-if="this.$store.state.user.role == 4"
                >å·¥äºº</el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="çœ" label-width="80px" size="mini" prop="provinceCode">
              <el-select
                placeholder="çœä»½"
                style="width: 150px"
                v-model="userInfoDialog.form.provinceCode"
                @change="loadCity"
              >
                <el-option
                  v-for="item in userInfoDialog.provinces"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label="å¸‚"
              label-width="80px"
              size="mini"
              prop="cityCode"
              v-if="userInfoDialog.form.role > 1"
            >
              <el-select
                placeholder="å¸‚"
                style="width: 150px"
                v-model="userInfoDialog.form.cityCode"
                @change="loadArea"
              >
                <el-option
                  v-for="item in userInfoDialog.cities"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label="åŽ¿"
              label-width="80px"
              size="mini"
              prop="areaCode"
              v-if="userInfoDialog.form.role > 2"
            >
              <el-select
                placeholder="åŽ¿"
                style="width: 150px"
                v-model="userInfoDialog.form.areaCode"
              >
                <el-option
                  v-for="item in userInfoDialog.areas"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </div>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="userInfoDialog.visible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click.native.prevent="handleUserDataSubmit">ç¡® å®š</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import http from "../../utils/http";
export default {
  name: "UserMgr",
  data() {
    return {
      labelposition: "left",
      searchText: "",
      userTypes: [
        { label: "è¶…çº§ç”¨æˆ·", value: "0" },
        { label: "çœçº§ç”¨æˆ·", value: "1" },
        { label: "å¸‚çº§ç”¨æˆ·", value: "2" },
        { label: "åŽ¿çº§ç”¨æˆ·", value: "3" },
        { label: "ç®¡ç†å‘˜", value: "4" },
        { label: "å·¥äºº", value: "5" }
      ],
      roleType: "",
      activeType: "",
      userData: {
        list: [],
        page: 1,
        limit: 10,
        total: 0,
        selectedIndex: -1

      },
      userInfoDialog: {
        isAdd: true,
        title: "",
        visible: false,
        form: {
          username: "",
          password: "",
          provinceCode: "",
          areaCode: "",
          cityCode: ""
        },
        provinces: [],
        cities: [],
        areas: [],
        rules: {
          username: [
            { required: true, message: "è¯·è¾“å…¥ç”¨æˆ·å", trigger: "blur" }
          ],
          password: [
            { required: true, message: "è¯·è¾“å…¥å¯†ç ", trigger: "blur" }
          ],
          repeat_password: [
            { required: true, message: "è¯·å†æ¬¡è¾“å…¥å¯†ç ", trigger: "blur" },
            {
              validator: (rule, value, callback) => {
                if (value != this.userInfoDialog.form.password) {
                  return callback(new Error("ä¸¤æ¬¡è¾“å…¥çš„å¯†ç ä¸ä¸€è‡´"));
                } else {
                  callback();
                }
              }
            }
          ],
          role: [{ required: true, message: "è¯·é€‰æ‹©è§’è‰²", trigger: "blur" }],
          provinceCode: [
            { required: true, message: "è¯·é€‰æ‹©çœ", trigger: "blur" }
          ],
          cityCode: [{ required: true, message: "è¯·é€‰æ‹©å¸‚", trigger: "blur" }],
          areaCode: [{ required: true, message: "è¯·é€‰æ‹©åŽ¿", trigger: "blur" }]
        }
      }
    };
  },
  methods: {
    handleRestPassword() {
      let username = this.userData.list[this.userData.selectedIndex].username;
      http.requestWithToken('/auth_api/user/reset_password', 'post', {targetUsername: username}, (res) => {
        if (!res.data.error) {
          this.$message.success('é‡ç½®å¯†ç æˆåŠŸ')
        }
        if (res.data.error) {
          this.$message.error('é‡ç½®å¯†ç å¤±è´¥')
        }
      })
    },
    handleUserDataSubmit() {
      if (!this.userInfoDialog.isAdd) {
        this.handleUserEdit()
        return;
      }
      let formData = this.userInfoDialog.form;
      let role = this.userInfoDialog.form.role;
      switch (role) {
        case 1:
          // çœçº§ç”¨æˆ·
          formData.adcode = formData.provinceCode;
          break;
        case 2:
          // å¸‚çº§ç”¨æˆ·
          formData.adcode = formData.cityCode;
          break;
        case 3:
          // åŽ¿çº§ç”¨æˆ·
          formData.adcode = formData.areaCode;
          break;
        case 4:
          // ç®¡ç†å‘˜
          formData.adcode = formData.areaCode;
          break;
        case 5:
          // å·¥äºº
          formData.adcode = formData.areaCode;
          break;
      }

      this.$refs.userInfoDialogForm.validate(valid => {
        if (valid) {
          http.requestWithTokenJson(
            "/auth_api/user",
            "post",
            formData,
            res => {
              if (res.data.error) {
                this.$message({
                  message: res.data.message,
                  type: "error"
                });
              } else {
                this.$message({
                  message: "æ·»åŠ æˆåŠŸ",
                  type: "success"
                });
                this.userInfoDialog.visible = true;
                this.loadUser();
              }
            },
            () => {}
          );
        } else {
        }
      });
    },

    handleUserDelete() {
      let username=this.userData.list[this.userData.selectedIndex].username;
      http.requestWithToken(
           "/auth_api/user_forbid",
           "put",
           {
              targetUser:username
           },
           res => {
                  if (!res.data.error) {
                      this.$message({
                      message: "åˆ é™¤æˆåŠŸ",
                      type: "success"
                    });
                  this.loadUser();
                  } else {
                              this.$message({
                              message: "åˆ é™¤å¤±è´¥",
                              type: "error"
                         });
                  }
           },
           () => {}
      );
    },

    loadUserActive() {
          if(this.roleType=="6"){
              this.roleType=""
          }
          if(this.activeType=="2"){
              this.activeType=""
          }
          http.requestWithToken(
            "/auth_api/user_list_active",
            "get",
            {
                searchText: this.searchText,
                limit: this.userData.limit,
                page: this.userData.page,
                roleType: this.roleType,
                active: this.activeType
            },
            res => {
                this.userData.list = res.data.data;
                this.userData.page = res.data.currentPage;
                this.userData.total = res.data.totalNum;
                this.userData.selectedIndex = -1;
            },
            () => {}
          );
        },
    handleUserEdit() {
      http.requestWithTokenJson(
        "/auth_api/user",
        "put",
        this.userInfoDialog.form,
        res => {
          if (!res.data.error) {
            this.$message({
              message: "ä¿®æ”¹æˆåŠŸ",
              type: "success"
            });
            this.userInfoDialog.visible = false;
            this.loadUser();
          } else {
            this.$message({
              message: "ä¿®æ”¹å¤±è´¥",
              type: "error"
            });
          }
        },
        () => {}
      );
    },

    loadUser() {
    if(this.roleType=="6"){
                  this.roleType=""
              }
              if(this.activeType=="2"){
                  this.activeType=""
              }
      http.requestWithToken(
        "/auth_api/user_list",
        "get",
        {
          searchText: this.searchText,
          limit: this.userData.limit,
          page: this.userData.page,
          roleType: this.roleType,
          active: this.activeType
        },
        res => {
          this.userData.list = res.data.data;
          this.userData.page = res.data.currentPage;
          this.userData.total = res.data.totalNum;
          this.userData.selectedIndex = -1;
        },
        () => {}
      );
    },
    handleUserDataCurrentPageChanged(val) {
      this.userData.page = val;
      this.loadUser();
    },
    showAddUser() {
      this.userInfoDialog.isAdd = true;
      this.userInfoDialog.visible = true;
      this.userInfoDialog.form = {
        province: "",
        area: "",
        city: "",
        active: true
      };
      this.loadProvince();
    },
    showEditUser() {
      this.userInfoDialog.isAdd = false;
      this.userInfoDialog.visible = true;
      this.userInfoDialog.form = {
        username: this.userData.list[this.userData.selectedIndex].username,
        name: this.userData.list[this.userData.selectedIndex].name,
        active: this.userData.list[this.userData.selectedIndex].active,
        phone: this.userData.list[this.userData.selectedIndex].phone,
        email: this.userData.list[this.userData.selectedIndex].email
      };

      this.loadProvince();
    },
    loadProvince() {
      http.requestWithToken(
        "/auth_api/dist/provinces",
        "get",
        {},
        res => {
          console.log(res.data);
          this.userInfoDialog.provinces = res.data;
          this.userInfoDialog.form.province = "";
          this.userInfoDialog.form.city = "";
          this.userInfoDialog.form.area = "";
          this.userInfoDialog.cities = [];
          this.userInfoDialog.areas = [];
          // this.userInfoDialog.form.area = ''
        },
        () => {}
      );
    },
    loadCity() {
      http.requestWithToken(
        "/auth_api/dist/cities",
        "get",
        { id: this.userInfoDialog.form.provinceCode },
        res => {
          this.userInfoDialog.cities = res.data;
          this.userInfoDialog.form.city = "";
          this.userInfoDialog.form.area = "";
          this.userInfoDialog.areas = [];
        },
        () => {}
      );
    },
    loadArea() {
      http.requestWithToken(
        "/auth_api/dist/areas",
        "get",
        { id: this.userInfoDialog.form.cityCode },
        res => {
          this.userInfoDialog.form.area = "";
          this.userInfoDialog.areas = res.data;
        },
        () => {}
      );
    }
  },
  mounted() {
    this.loadUser();
  }
};
</script>

<style>
#tool-row {
  display: flex;
  justify-content: space-between;
}
#userInfoDialogData {
  display: flex;
  justify-content: space-around;
}
</style>



