<template>
  <div>
    <div id="tool-row">
      <div>
        <el-input v-model="searchText" placeholder="æœç´¢" style="width: 150px"></el-input>
        <el-date-picker
          v-model="maintenanceData.startDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="èµ·å§‹æ—¥æœŸ"
        ></el-date-picker>
        <el-date-picker
          v-model="maintenanceData.endDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="ç»ˆæ­¢æ—¥æœŸ"
        ></el-date-picker>
        <el-button type="primary" @click="loadMaintenanceData">æœç´¢</el-button>
      </div>

      <div>
      <el-button
                type="primary"
                @click="handleReportMaintenanceData"
                v-if="this.$store.state.user.role == 3"
              >ä¸ŠæŠ¥</el-button>
      </div>
    </div>
    <div style="padding-top:5px">
      <el-table
        border
        :data="maintenanceData.list"
        style="width: 100%"
        height="600"
        @selection-change="handleMaintenanceDataSelectionChange"
      >
        <!-- <el-table-column label width="50" align="center" fixed="left">
          <template scope="scope">
            <el-radio
              :label="scope.$index"
              v-model="maintenanceData.selectedIndex"
              @change.native="getCurrentRow(scope.$index)"
            >&nbsp</el-radio>
          </template>
        </el-table-column>-->
        <el-table-column type="selection" width="55" fixed="left"></el-table-column>
        <el-table-column prop="deviceId" label="è®¾å¤‡ID"></el-table-column>
        <el-table-column prop="batch" label="æ‰¹æ¬¡"></el-table-column>
        <el-table-column prop="num" label="æ¾å¢¨å¤©ç‰›æ•°é‡"></el-table-column>
        <el-table-column label="å…¶ä»–å¤©ç‰›ç±»åž‹">
          <template slot-scope="scope">
            {{otherBeetleDict["t" + scope.row.otherType]}}
          </template>
        </el-table-column>
        <el-table-column prop="otherNum" label="å…¶ä»–å¤©ç‰›æ•°é‡"></el-table-column>
        <el-table-column prop="longitude" label="ç»åº¦"></el-table-column>
        <el-table-column prop="latitude" label="çº¬åº¦"></el-table-column>
        <el-table-column prop="altitude" label="æµ·æ‹”"></el-table-column>
        <el-table-column label="ä½ç½®" width="200px">
          <template
            slot-scope="scope"
          >{{scope.row.province + scope.row.city + scope.row.area + scope.row.town}}</template>
        </el-table-column>
        <el-table-column prop="username" label="ç”¨æˆ·å"></el-table-column>
        <el-table-column prop="date" label="æ—¥æœŸ"></el-table-column>
        <el-table-column label="å·¥ä½œå†…å®¹">
          <template
            slot-scope="scope"
          >{{maintenanceData.workingContentDict[scope.row.workingContent]}}</template>
        </el-table-column>
        <el-table-column prop="drug" label="è¯å‰‚ç±»åž‹"></el-table-column>
        <el-table-column prop="remark" label="å¤‡æ³¨"></el-table-column>
        <el-table-column label="çŽ°åœºç…§ç‰‡" width="100px" align="center">
          <template slot-scope="scope">
            <el-button
              @click="showPhotoDialog(scope.row.imageId)"
              v-if="scope.row.imageId != null && scope.row.imageId !=''"
              size="mini"
            >æ˜¾ç¤º</el-button>
          </template>
        </el-table-column>
        <el-table-column
                  label="æ“ä½œ"
                  align="center"
                  width="150px"
                  fixed="right"
                  v-if="this.$store.state.user.role == 6 || this.$store.state.user.role == 3"
                >
                  <template slot-scope="scope">
                    <div v-if="!scope.row.reported">
                      <el-button
                        size="mini"
                        type="primary"
                        @click="showEditMaintenanceDataDialog(scope.row)"
                        v-if="!scope.row.reported"
                      >ç¼–è¾‘</el-button>
                      <el-button
                          size="mini"
                          type="danger"
                          @click="handleDelete(scope.row)"
                          v-if="!scope.row.reported"
                      >åˆ é™¤</el-button>

                    </div>
                    <div v-if="scope.row.reported">ä¸å¯æ“ä½œ</div>
                  </template>
                </el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          background
          @current-change="handleMaintenanceDataCurrentPageChanged"
          :current-page.sync="maintenanceData.page"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="maintenanceData.total"
        ></el-pagination>
      </div>
    </div>
    <el-dialog title="çŽ°åœºç…§ç‰‡" :visible.sync="PhotoDialog.visible" width="700px">
      <div style="overflow-y:scroll;height: 300px">
        <img v-bind:src="PhotoDialog.pic" style="width: 600px; ">
      </div>
    </el-dialog>
    <el-dialog title="çŽ°åœºç…§ç‰‡" :visible.sync="PhotoDialog.visible" width="700px">
      <div style="overflow-y:scroll;height: 300px">
        <img v-bind:src="PhotoDialog.pic" style="width: 600px; ">
      </div>
    </el-dialog>
    <!-- ç¼–è¾‘ç»´æŠ¤ä¿¡æ¯å¯¹è¯æ¡† -->
    <el-dialog title="ç¼–è¾‘ç»´æŠ¤ä¿¡æ¯" :visible.sync="EditMaintenanceDialog.visible" width="30%">
      <el-form label-width="120px">

        <el-form-item label="ç»åº¦">
          <el-input style="width:150px" v-model="EditMaintenanceDialog.form.longitude"></el-input>
        </el-form-item>
        <el-form-item label="çº¬åº¦">
          <el-input style="width:150px" v-model="EditMaintenanceDialog.form.latitude"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="EditMaintenanceDialog.visible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click.native.prevent="handleEditMaintenanceDataSubmit">ç¡® å®š</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import http from "../../utils/http";
export default {
  data() {
    return {
      searchText: "",
      otherBeetleList: [],
      otherBeetleDict: {},
      checkBoxData: [],
      maintenanceData: {
        list: [],
        selectedIndex: -1,
        page: 1,
        limit: 10,
        total: 0,
        startDate: null,
        endDate: null,
        selection: [],
        workingContentDict: ["é¦–æ¬¡æ‚¬æŒ‚è¯±æ•å™¨", "æ¢è¯+æ”¶è™«", "æ”¶è™«", "å…¶ä»–"]
      },
      PhotoDialog: {
        visible: false,
        pic: ""
      },
      EditMaintenanceDialog: {
        visible: false,
        form: {
          id: 0,
          num: 0,
          otherNum: 0,
          otherType: "",
          longitude: "",
          latitude: "",
          workingContent: 0,
          drug: ""
        }
      }
    };
  },
  methods: {

  handleUserDelete(){
      let data = [];
        for (let i = 0; i < this.maintenanceData.selection.length; ++i) {
          data.push(this.maintenanceData.selection[i].id);
        }
        http.requestWithTokenJson(
          "/auth_api/maintenance/abnormal",
          "post",
          { list: data },
          res => {
            if (!res.data.error) {

              this.$message.success("ä¸ŠæŠ¥æˆåŠŸ");
              // this.maintenanceData.page = 1;

            }else{
            this.$message.error("ä¸ŠæŠ¥å¤±è´¥");
            }

            },

          () => {}
        );
loadMaintenanceData();
     },
    setSearchText(searchText) {
      this.searchText = searchText;
    },
    handleDelete(row){
            this.$confirm('æ­¤æ“ä½œå°†æ°¸ä¹…åˆ é™¤è¯¥æ–‡ä»¶, æ˜¯å¦ç»§ç»­?', 'æç¤º', {
                      confirmButtonText: 'ç¡®å®š',
                      cancelButtonText: 'å–æ¶ˆ',
                      type: 'warning'
                    }).then(() => {
                    http.requestWithToken(
                             "/auth_api/maintenance/abnormal",
                             "delete",
                             {
                               id:row.id,
                               deviceID:row.deviceId
                             },
                             res => {
                  if (!res.data.error) {
                  this.loadMaintenanceData();
                        this.$message({
                          type: 'success',
                          message: 'åˆ é™¤æˆåŠŸ!'
                      });
                      }else{
                      this.$message({
                                            type: 'error',
                                            message: 'åˆ é™¤å¤±è´¥!'
                                        });
                      }
                             },
                             () => {}
                           );

                    }).catch(() => {
                      this.$message({
                        type: 'info',
                        message: 'å·²å–æ¶ˆåˆ é™¤'
                      });
                    });
    this.loadMaintenanceData();
        },
    // è¡¨æ ¼ç›¸å…³
    loadMaintenanceData() {
      this.loadOtherBeetleType();
      http.requestWithToken(
        "/auth_api/maintenance/abnormal",
        "get",
        {
          condition: this.searchText,
          page: this.maintenanceData.page,
          limit: this.maintenanceData.limit,
          startDate: this.maintenanceData.startDate,
          endDate: this.maintenanceData.endDate
        },
        res => {
          this.maintenanceData.list = res.data.data;
          this.maintenanceData.total = res.data.totalNum;
        },
        () => {}
      );
    },
    handleMaintenanceDataCurrentPageChanged(val) {
      this.maintenanceData.page = val;
      this.loadMaintenanceData();
    },
    //
    showPhotoDialog(id) {
      this.PhotoDialog.visible = true;
      let BASE_URL = "http://47.103.66.70:8081";
      //let BASE_URL = "http://39.108.184.47:8081";
      this.PhotoDialog.pic = BASE_URL + "/device_img?imgName=" + id;
    },
    handleMaintenanceDataSelectionChange(val) {
      this.maintenanceData.selection = val;
      console.log(val);
    },
    showEditMaintenanceDataDialog(data) {
      this.EditMaintenanceDialog.visible = true;
            this.EditMaintenanceDialog.form = {
              id: 0,
              num: 0,
              otherNum: 0,
              otherType: "",
              longitude: "",
              latitude: "",
              workingContent: 0,
              drug: ""
            };
            this.EditMaintenanceDialog.form.longitude = data.longitude;
            this.EditMaintenanceDialog.form.latitude = data.latitude;
            this.EditMaintenanceDialog.form.workingContent = data.workingContent;
            this.EditMaintenanceDialog.form.drug = data.drug;
            this.EditMaintenanceDialog.form.num = data.num;
            this.EditMaintenanceDialog.form.id = data.id;
            this.EditMaintenanceDialog.form.otherNum = data.otherNum;
            this.EditMaintenanceDialog.form.otherType = data.otherType;
    },
    handleEditMaintenanceDataSubmit() {
      http.requestWithTokenJson(
        "/auth_api/maintenance/abnormal",
        "put",
        this.EditMaintenanceDialog.form,
        res => {
          this.$message.success("ä¿®æ”¹æˆåŠŸ");
          this.EditMaintenanceDialog.visible = false;
          this.loadMaintenanceData();
        },
        () => {}
      );
    },
    loadOtherBeetleType() {
      http.requestWithToken(
        "/auth_api/other_beetle",
        "get",
        {},
        res => {
          this.otherBeetleList = res.data.data;
          for (let i = 0; i < this.otherBeetleList.length; ++i) {
            this.otherBeetleDict[
              "t" + this.otherBeetleList[i].id
            ] = this.otherBeetleList[i].name;
          }
        },
        () => {}
      );
    },
    handleReportMaintenanceData() {
      let data = [];
      for (let i = 0; i < this.maintenanceData.selection.length; ++i) {
        data.push(this.maintenanceData.selection[i].id);
      }
      http.requestWithTokenJson(
        "/auth_api/maintenance/abnormal/report",
        "post",
        { list: data },
        res => {
          if (!res.data.error) {
            this.$message.success("ä¸ŠæŠ¥æˆåŠŸ");
            this.loadMaintenanceData();
            // this.maintenanceData.page = 1;

          }else{
          this.$message.error("ä¸ŠæŠ¥å­˜åœ¨å¤±è´¥");
          this.loadMaintenanceData();
          }
        },
        () => {}
      );

    },
    exportExcel() {
      window.location =
        http.getBaseUrl() +
        "/maintenance/export?startDate=" +
        this.maintenanceData.startDate +
        "&endDate=" +
        this.maintenanceData.endDate +
        "&token=" +
        sessionStorage["token"];
    }
  },
  mounted() {
    this.loadMaintenanceData();
    // this.loadOtherBeetleType();
  }
};
</script>
<style>
#tool-row {
  display: flex;
  justify-content: space-between;
}
#userInfoDialogData {
  display: flex;
  justify-content: space-around;
}
</style>





